import request from '@/utils/request'

// 帖子列表
function threadlist(params) {
  return request({
    url: '/community/threadlist',
    method: 'get',
    params: params
  })
}
// 评论列表
function commentlist(params) {
  return request({
    url: '/community/commentlist',
    method: 'get',
    params: params
  })
}
// 评论回复列表
function commentreply(params) {
  return request({
    url: '/community/commentreply',
    method: 'get',
    params: params
  })
}
// 帖子详情
function threaddetail(params) {
  return request({
    url: '/community/threaddetail',
    method: 'get',
    params: params
  })
}
// 审核待审核发帖、评论
function GiftInsert(data) {
  return request({
    url: '/community/handling',
    method: 'post',
    data
  })
}
// 设置推荐
function tuijieSet(data) {
  return request({
    url: '/community/set',
    method: 'post',
    data
  })
}
// 删除
function delHandles(data) {
  return request({
    url: '/community/delete',
    method: 'post',
    data
  })
}

//视频导入列表，最右
function importList(params) {
  return request({
    url: '/community/import/list',
    method: 'get',
    params: params
  });
}
//加入视频采集队列，最右
function importAdd(data) {
  return request({
    url: '/community/import/add',
    method: 'post',
    data
  });
}

//视频导入列表，抖音
function dyimportList(params) {
  return request({
    url: '/community/dyimport/list',
    method: 'get',
    params: params
  });
}
//加入视频采集队列，抖音
function dyimportAdd(data) {
  return request({
    url: '/community/dyimport/add',
    method: 'post',
    data
  });
}

//修改外部导入发现数据的内容
function communityModify(data) {
  return request({
    url: '/community/modify',
    method: 'post',
    data
  });
}
//审核评论
function communityAudit(data) {
  return request({
    url: '/community/commentHandling',
    method: 'post',
    data
  });
}
//只看评论列表
function onlyCommunity(params) {
  return request({
    url: '/community/commentOnly',
    method: 'get',
    params: params
  });
}
export default {
  threadlist,
  commentlist,
  commentreply,
  threaddetail,
  GiftInsert,
  tuijieSet,
  delHandles,
  importList,
  importAdd,
  dyimportList,
  dyimportAdd,
  communityModify,
  communityAudit,
  onlyCommunity
}
