import request from '@/utils/request'

// app列表
function appListData(params) {
  return request({
    url: '/app/list',
    method: 'get',
    params: params
  })
}

//添加
function addApp(data) {
  return request({
    url: '/app/insert',
    method: 'post',
    data
  })
}

// app详情
function appDetail(params) {
  return request({
    url: '/app/detail',
    method: 'get',
    params: params
  })
}

//添加
function updateApp(data) {
  return request({
    url: '/app/update',
    method: 'post',
    data
  })
}

//删除
function deleteApp(data) {
  return request({
    url: '/app/delete',
    method: 'post',
    data
  })
}

// 还原
function recoveryApp(data) {
  return request({
    url: '/app/recovery',
    method: 'post',
    data
  })
}

// 彻底删除
function realDeleteApp(data) {
  return request({
    url: '/app/realDelete',
    method: 'post',
    data
  })
}

// button列表
function buttonListData(params) {
  return request({
    url: '/app/button/list',
    method: 'get',
    params: params
  })
}
// button详情
function buttonDetail(params) {
  return request({
    url: '/app/button/detail',
    method: 'get',
    params: params
  })
}
//button删除
function deleteButton(data) {
  return request({
    url: '/app/button/delete',
    method: 'post',
    data
  })
}
//button编辑
function updateButton(data) {
  return request({
    url: '/app/button/update',
    method: 'post',
    data
  })
}
//button添加
function insertButton(data) {
  return request({
    url: '/app/button/insert',
    method: 'post',
    data
  })
}
// 还原
function recoveryButton(data) {
  return request({
    url: '/app/button/recovery',
    method: 'post',
    data
  })
}
// 彻底删除
function realDeleteButton(data) {
  return request({
    url: '/app/button/realDelete',
    method: 'post',
    data
  })
}

// 获取上传令牌
function getuploadFile(data) {
  return request({
    url: '/file/uploadFile',
    method: 'post',
    data
  })
}
//  
function getfileSts(params) {
  return request({
    url: '/file/sts',
    method: 'get',
    params: params,
  })
}
export default {
  appListData,
  addApp,
  appDetail,
  updateApp,
  deleteApp,
  buttonListData,
  buttonDetail,
  deleteButton,
  updateButton,
  insertButton,
  recoveryApp,
  recoveryButton,
  realDeleteApp,
  realDeleteButton,
  getuploadFile,
  getfileSts
}
