import request from '@/utils/request'

//概览今日统计
function overviewCount(){
    return request({
        url: '/overview/count',
        method: 'get'
    })
}
//用户注册图表
function registerChart(params){
    return request({
        url: '/overview/registerChart',
        method: 'get',
        params
    })
}
//用户充值图表
function rechargeChart(params){
    return request({
        url: '/overview/rechargeChart',
        method: 'get',
        params
    })
}
export default {
    overviewCount,
    registerChart,
    rechargeChart
}